<template>
  <div class="landing">
    <img src="/movie.jpg" alt="" />
    <div class="form">
      <b-tabs content-class="mt-3">
        <b-tab title="Register" active @click="errorMessage = ''">
          <b-form @submit="submitRegister" class="register">
            <b-form-group label="Name">
              <b-form-input
                v-model="register.name"
                type="text"
                required
              ></b-form-input>
            </b-form-group>
            <b-form-group label="Email">
              <b-form-input
                v-model="register.email"
                type="email"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group label="Password">
              <b-form-input
                v-model="register.password"
                type="password"
                required
              ></b-form-input>
            </b-form-group>
            <b-form-group label="Confirm password">
              <b-form-input
                v-model="register.password2"
                type="password"
                required
              ></b-form-input>
            </b-form-group>
            <div class="submit-wrapper">
              <b-button type="submit">Submit</b-button>
              <span>or</span>
            </div>
            <b-button class="guest" @click="logInAsGuest"
              >login as guest</b-button
            >
            <b-alert
              fade
              variant="danger"
              :show="errorMessage !== ''"
              dismissible
            >
              {{ errorMessage }}
            </b-alert>
          </b-form>
        </b-tab>
        <b-tab title="Login" @click="errorMessage = ''">
          <b-form @submit="submitLogin">
            <b-form-group label="Email">
              <b-form-input
                v-model="login.email"
                type="email"
                required
              ></b-form-input>
            </b-form-group>
            <b-form-group label="Password">
              <b-form-input
                v-model="login.password"
                type="password"
                required
              ></b-form-input>
            </b-form-group>
            <b-button type="submit">Submit</b-button>

            <b-alert
              fade
              variant="danger"
              :show="errorMessage !== ''"
              dismissible
            >
              {{ errorMessage }}
            </b-alert>
          </b-form>
        </b-tab>
      </b-tabs>
    </div>
  </div>
</template>

<script>
import fetchServerData from "../functions/fetchServerData";

export default {
  name: "Landing",

  data() {
    return {
      register: {
        name: "",
        email: "",
        password: "",
        password2: "",
      },
      login: {
        email: "",
        password: "",
      },
      errorMessage: "",
    };
  },
  props: ["user"],
  mounted() {
    if (this.user.id) {
      this.$router.push("/rate");
    }
  },
  methods: {
    logInAsGuest: async function() {
      try {
        const api = await fetchServerData("post", "/auth/login", {
          email: "guest@guest.com",
          password: "abcd1234",
        });
        if (api.status === 200) {
          localStorage.setItem("user-token", api.data.tokens.access.token);
          this.$emit("login", api.data.user);
        }
        if (api.data.message) this.displayError(api.data.message);
      } catch (error) {
        console.log(error.message);
      }
    },
    submitRegister: async function() {
      try {
        if (this.register.password !== this.register.password2) {
          this.displayError("Passwords don't match!");
        } else {
          const { name, password } = this.register;
          const nameUpperCase = name.charAt(0).toUpperCase() + name.slice(1);

          const email = this.register.email.toLowerCase();
          const data = {
            name: nameUpperCase,
            email,
            password,
          };
          const api = await fetchServerData("post", "/auth/register", data);
          if (api.status === 201) {
            localStorage.setItem("user-token", api.data.tokens.access.token);
            this.$emit("login", api.data.user);
          } else {
            console.log(api);
            if (api.data.message) this.displayError(api.data.message);
          }
        }
      } catch (error) {
        console.log(error.message);
      }
    },
    submitLogin: async function() {
      const { password } = this.login;
      const email = this.login.email.toLowerCase();
      const data = {
        email,
        password,
      };
      try {
        const api = await fetchServerData("post", "/auth/login", data);
        if (api.status === 200) {
          localStorage.setItem("user-token", api.data.tokens.access.token);
          this.$emit("login", api.data.user);
        } else {
          console.log(api.data.message);
          if (api.data.message) this.displayError(api.data.message);
        }
      } catch (error) {
        console.log(error.message);
      }
    },
    displayError: function(message) {
      this.errorMessage = "";
      setTimeout(() => {
        this.errorMessage = message;
      }, 200);
    },
  },
};
</script>

